.comments{
	margin-top: 10px;
	// display: none;
	background-color: rgba(122, 22, 252, 0.075);
	border-radius: 10px;
	padding: 10px;
	box-sizing: border-box;
	position: relative;
}
.commentsList{
	max-height: 230px;
	overflow-x: hidden;
	overflow-y: scroll;
	&::-webkit-scrollbar{

		width: 2px;
		height: 0px;
	}
	&::-webkit-scrollbar-track {
		// box-shadow: 5px 5px 5px -5px rgba(34, 60, 80, 0.2) inset;
		background-color: #f9f9fd;
		border-radius: 10px;
	  }
	  
	&::-webkit-scrollbar-thumb {
		border-radius: 10px;
		background: grey;
	}
}
.comments.active{

	position: relative;
	z-index: 1;
	// border-top: 2px solid hsl(223, 19%, 93%);
	
	// padding-right: 10px;
	display: flex;
	height: auto;
	flex-direction: column;
	animation-name: slide, opacitier, blockheigth;
	animation-duration: 1s, 1s, 1s;
	animation-timing-function: linear, linear, linear;
}

