body{
	max-height: 95vh;
}
.Weather{
	max-height: auto;
	max-width: 90vw;
	justify-content: center;
	
	display: flex;
	overflow: hidden;
	margin: 0 auto;
	margin-top: 65px;
}
.weather{
	text-align: center;
	margin: 1% 0;
	width: 85vw;
	height: auto;
	padding: 10px 30px;
	background: #B993D6;  /* fallback for old browsers */
	background: -webkit-linear-gradient(to right, #8CA6DB, #B993D6);  /* Chrome 10-25, Safari 5.1-6 */
	background: linear-gradient(to right, #8CA6DB, #B993D6); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
	border-radius: 30px;
	
}
.queries{
	margin-top:15px;
	display:flex;
	justify-content:space-between;
	text-align: center;
	height: 30px;
}
.Cards{
	margin:0px auto;
	box-sizing: border-box;
	margin-top: 20px;
	width:80%;
	height: 60vh;
	/* position: relative; */
	/* display: flex; */
	justify-content: space-between;
}
.carousel{
	
}
.todaysCard{
	box-sizing: border-box;
	position: relative;
	background-color: #fff;
	border-radius: 20px;
	font-size: 20px;
	text-align: left;
	padding: 20px;
	flex: 0 1 100%;
	height: 60vh;
	width: 100%;

	display: flex;
	flex-direction: column;
	justify-content: space-between;
}

.datetemp{
	display: grid;
	grid-column-gap: 20px;
	grid-template-columns: 3fr 1fr;
	grid-template-rows: 1fr 1fr;
	grid-template-areas: "time temp"
						 "place temp";
}

.datetime{
	grid-area: time;
	color: #046310;
	font-weight: bold;
	flex: 0 1 20%;

}
.place{
	grid-area: place;
	margin-top: 10px;
	font-size: 25px;
	font-weight: bold;
}
.temperature{
	grid-area: temp;
	font-size: 30px;
	font-weight: bold;
	display: flex;
	align-content: center;
	margin: auto;
	justify-content: center;
}
.temperature div{
	text-align: center;
	align-self: center;
}
.temperature img{
	margin: auto;
	width: 40px;
	height: 40px;
}
.feels{
	flex: 0 1 20%;
}
.conditions{
	flex: 0 0 40%;
	position: relative;
	display: grid;
	grid-template-columns: 1.2fr 1fr;
	grid-template-rows: 1fr 1fr 1fr 1fr 1fr 1fr;
	grid-gap: 5px;
	font-size: 16px;
	width: 100%;
	border-left: 2px lightblue solid;
	padding-left: 10px;
}

.todaysGraph{
	
	box-sizing: border-box;
	width: 100%;

	height: 100%;
	padding: 20px;
	padding-left: 10px;
	border-radius: 20px;
	background-color: white;
}
.graphsTitle{
	font-family: 'Grape Nuts', cursive;
	font-size: 30px;
	height: 13%;
}
.chartGraph{
	
	position: relative;
	overflow: auto;
	box-sizing: border-box;
	background-color: white;
	height: 87%;
}
.chartGraph::-webkit-scrollbar {
	width: 7px;
	height: 7px;
  }
  
  .chartGraph::-webkit-scrollbar-track {
	box-shadow: 5px 5px 5px -5px rgba(34, 60, 80, 0.2) inset;
	background-color: #f9f9fd;
	border-radius: 10px;
  }
  
  .chartGraph::-webkit-scrollbar-thumb {
	border-radius: 10px;
	background: linear-gradient(180deg, #00c6fb, #005bea);
  }
.weekCard{
	box-sizing: border-box;
	width: 100%;

	height: 60vh;
	padding: 10px	;
	background-color: white;
	border-radius: 20px;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
}
.forecastTitle{
	padding: 5px 0;
	font-family: 'Grape Nuts', cursive;
	font-size: 25px;
	flex: 0 1 13%;
}
.forecastOtherDays{
	box-sizing: border-box;
	border-radius: 20px;
	width: 100%;
	flex: 0 1 87%;
	overflow-y: scroll;
}
.forecastOtherDays::-webkit-scrollbar {
	width: 7px;
	height: 7px;
	display: none;
  }
  
  .forecastOtherDays::-webkit-scrollbar-track {
	margin: 7px 0;
	box-shadow: 5px 5px 5px -5px rgba(34, 60, 80, 0.2) inset;
	background-color: #f9f9fd;
	border-radius: 10px;
  }
  
  .forecastOtherDays::-webkit-scrollbar-thumb {
	border-radius: 10px;
	background: linear-gradient(180deg, #00c6fb, #005bea);
  }


  .loader{
	  display: flex;
	  justify-content: center;
	  align-content: center;
	  background-color: white;
	  border-radius: 20px;
	  height: 100%;
  }
  #loader1{
	width: 32%;
	height: 100%;
  }

  @media(max-width:1024px){
	#root{
		width: 100%;
		height: 100%;}
	/* .App{
		width: 500px;
		height: 100%;
		margin: 0px;
		padding: 25px 30px;
	} */
	.title{
		font-size: 35px;
		padding: 20px 10px;
	}
	.queries{
		display: grid;
		grid-template-columns: 1fr 1fr;
		grid-template-rows: 1fr 1fr;
		row-gap: 10px;
	}
	.Cards{
		width: 100%;
		height: 1500px;
		display: flex;
		justify-content: space-between;
		flex-direction: column;
		padding: 0px;
	}
	.todaysCard{
		width: 100%;
		flex: 0 1 32%;
	}
	.todaysGraph{
		width: 100%;
		flex: 0 1 32%;
	}
	.weekCard{
		width: 100%;
		height: 32%;
	}
	.forecastOtherDays{
		height: 80%;
		overflow: scroll;
	}
	#loader1{
		width: 100%;
	}
	.loader{
		flex: 0 0 32%;
	}
  }

  @media(max-width:1200px){
	.temperature{
		flex-direction: column;
	}
	
}