.tape{
	box-sizing: border-box;
	margin: 0;
	border-radius: 20px;
	width: 100%;
	margin-bottom: 3px;
	background-color: rgb(212, 255, 255);
	border: 3px solid lightblue;
	display:grid;
	grid-template-columns: 1.2fr 2.3fr;
	grid-template-rows: 1fr 2.3fr;
	grid-template-areas: "datetemp  description"
						 "datetemp  body";
	padding: 6px;
	height: 33%;
}
.description{
	grid-area: description;
	font-size: 1vw;
}
.datetemp{
	grid-area: datetemp;
}
.datetime{
	font-size: 1.1vw;
}
.temp{
	display: flex;
	flex-direction: row;
	justify-content: center;
	align-items: center;
	font-size: 1.2vw
}
.temp img{
	width: 60px;
	height: 60px;
}
.body{
	grid-area: body;
	padding: 7px 0;
}