.post-enter {
	transform: translateX(0px);

}
.post-enter-active {
	transform: translateX(0px);
	transition: all 500ms ease-in;
}
.post-exit {
	opacity: 1;
}
.post-exit-active {
	transform: translateX(-350px);
	transition: all 500ms ease-in;
}
$transition-duration: .7s;
.accordion-item {
  }
  
  .content {
	max-height: 0;
	overflow: hidden;
	transition: max-height $transition-duration;
	
	&.active {
	  max-height: 350px;
	}
  }
  
  .content-inner {
	opacity: 0;
	visibility: hidden;
	transform: translateY(10px);
	transition: opacity $transition-duration, visibility $transition-duration, transform $transition-duration;
	
	.active & {
	  opacity: 1;
	  visibility: visible;
	  transform: translateY(0);
	}
  }



