.comment{
	display: flex;
	flex:0 0 20%;
	margin-bottom: 12px;
	border-radius: 20px;
	box-sizing: border-box;
	box-shadow: 0px 0px 5px 1px rgba(97, 67, 67, 0.404);
	// background-color: rgba(248, 230, 252, 0.324);
	background-color: #fff;
	padding: 5px 5px;
	:last-child{
		margin-bottom: 0px;
	}
}

.ava img{
	max-width: 30px;
	border-radius: 50%;
}
.body{
	// width: 100%;
	// width: 80%;
	margin-left: 10px;
	display: flex;
	justify-content: start;
	align-items: start;
	text-align: left;
	flex-direction: column;
}