.center {
	right: 0;
	top: 50%;
	right:40px;
	transform: translate(-50%, -50%);
	position: absolute;
	width: 40px;
  }
  
  .center:before,
  .center:after,
  .center div {
	background: rgb(200, 192, 232);
	content: "";
	display: block;
	height: 6px;
	border-radius: 3px;
	margin: 4px 0;
	transition: 0.5s;
  }
  .active:before {
	transform: translateY(12px) rotate(135deg);
  }
  .active:after {
	transform: translateY(-9px) rotate(-135deg);
  }
  .active div {
	transform: scale(0);
  }