.CreateComment{
	position: relative;
	width: auto;
	display: flex;
	height: auto;
	margin-top: 10px;
	border-radius: 10px;
}

.image{
	max-width: 30px;
	max-height: 30px;
	border-radius: 50%;
	margin: auto;
}
.emoji{
	background-color: transparent;
}
.text{
	flex: 0 1 90%;
	outline:none;
	border: 0;
	border-radius: 5px;
	width: auto;
	margin: 0 10px;
	font-size: 18px;
	resize:none;
}
.btn{
	color: black;
	border-radius: 10px;
	background-color: rgba(151, 81, 151, 0.148);
}