.body{
	position: relative;
	flex:10%;
	height: 50px;
	display: flex;
	text-align: center;
	justify-content: space-between;
	width: 60%;
	align-items: center;
}

// .likesContainer{
// }
.likesWindowContainer{
	display: none;
	flex-direction: row;
	justify-content: center;
	align-items: center;
	

	position: absolute;
	
	// left: -19px;

	bottom: 0px;
	max-width:130px;
	min-width:30px;
	padding-bottom: 40px;
}

.dislikesWindowContainer{
	display: none;
	flex-direction: row;
	justify-content: center;
	align-items: center;
	

	position: absolute;
	
	// left: -19px;

	bottom: 0px;
	max-width:130px;
	min-width:30px;
	padding-bottom: 40px;
}
.comments, .like, .dislike{
	position: relative;
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	background-color: rgb(240, 235, 243);
	transition: background-color 2s ease;
	padding: 2px 7px;
	border-radius: 10px;

	align-items: center;
	img{
		margin-right: 5px;
		max-height: 30px;
	}
}
.like{
 img{
	 margin-bottom: 5px;
 }
}
.dislike{
	img{
		margin-top: 5px;
	}
}

.comments{
	padding: 5px 10px;
	img{
		max-height: 30px;
		margin-right: 8px;
	}
}

.like.active, .dislike.active{
	background-color: rgb(230, 189, 254);
	animation: liked 1.5s linear;
	img{	
	}
}
@keyframes liked {
	0%{	
		transform: scale(1);
	}
	70%{
		transform: scale(1.3);
	}
	100%{
		transform: scale(1);
	}
}

.likesWindow{
	background-color: rgba(black, 0.5);
	border-radius: 10px;
	padding: 5px;
	height: 40px;
	max-width:120px;
	min-width:30px;

	.likesList{
		display: flex;
		justify-content: center;
		align-items: center;
		&::-webkit-scrollbar{

			// width: 2px;
			height: 2px;
		}
		&::-webkit-scrollbar-track {
			// box-shadow: 5px 5px 5px -5px rgba(34, 60, 80, 0.2) inset;
			background-color: #f9f9fd;
			border-radius: 10px;
		  }
		  
		&::-webkit-scrollbar-thumb {
			border-radius: 10px;
			background: grey;
		}
	}
	.image__wrapper{
		width: 25px;
	}
	.image{
		border: 5px;
		margin: 2px;
		border-radius: 50%;
		max-width: 30px;
		max-height: 30px;
		margin: 0px;
		// background-color: rgba(black, 0.4);
	}
	.textImage{
		background-color: rgba(rgb(100, 78, 78), 0.4);
		box-shadow: 0px 0px 2px 2px#f9f9fd;
		border-radius: 50%;
		display: flex;
		align-items: center;
		justify-content: center;
		width: 30px;
		height: 30px;
		margin: 0px;
	}
}


