.body{
	box-sizing: border-box;
	position: relative;
	height: 90%;
	width: 100%;
	margin-top: 80px;
}
.file{
	opacity: 0;
	visibility: hidden;
	position: absolute;
}
.createDialog{

}
.button{
	color: black;
	border-radius: 10px;
	background-color: rgba(151, 81, 151, 0.148);
}
.text{
	&::-webkit-scrollbar{

		width: 2px;
		height: 0px;
	}
	&::-webkit-scrollbar-track {
		// box-shadow: 5px 5px 5px -5px rgba(34, 60, 80, 0.2) inset;
		background-color: #f9f9fd;
		border-radius: 10px;
	  }
	  
	&::-webkit-scrollbar-thumb {
		border-radius: 10px;
		background: grey;
	}
}

.noScroll{
	box-sizing: border-box;
	padding: 10px;
}

.noScroll::-webkit-scrollbar {
	width: 5px;
  }
  
.noScroll::-webkit-scrollbar-track {
	box-shadow: 5px 5px 5px -5px rgba(34, 60, 80, 0.2) inset;
	background-color: #f9f9fd;
	border-radius: 10px;
  }
  
.noScroll::-webkit-scrollbar-thumb {
	border-radius: 10px;
	background: linear-gradient(180deg, #d396f7, #a000ea);
  }