.modal{
	position:fixed;
	top:45px;
	bottom: 0;
	right: 0;
	left: 0;
	display: none;
	background-color: rgba(234, 225, 246, 0.37);
}
.container{
	position: relative;
	display: flex;
	padding: 25px;
	background-color: white;
	border-radius: 16px;
	max-width: auto;
	max-height: auto;
}
.photoContent{
	position: relative;
	background-color: rgba(228, 218, 251, 0.336);
	display: flex;
	align-items: center;
	justify-content: center;
	min-width: 40vw;
	max-width: 50vw;
	min-height: 60vh;
	max-height: 75vh;
	img{
		max-width: 40vw;
		max-height: 60vh;
	}
}

.modal.active{
	
	display: flex;
	justify-content: center;
	align-content: center;
	justify-content: center;
	align-items: center;
	z-index: 3;
}
.photoContent__body{
	
	box-sizing: border-box;
	max-height: 100%;
	max-width: 100%;
	width: 1;
}
.prev,.next{
	box-sizing: content-box;
	height: 100%;
	width: 30%;
	z-index: 4;
	position: absolute;
	background-color: rgba(black, 0.0001);
	padding: 0px;
}
.prev:hover,.next:hover{
	background-color: rgba(black, 0.2);
}
.prev{
	left: 0px;

}
.next{
	right: 0px;
}
.body{
	display: flex;
	flex-direction: column;
}
.body__title{
	margin-bottom: 10px;
	flex: 0 1 15%;
	display: flex;
	flex-direction: row;
}
.avatar{

	img{
		border-radius: 50%;
		overflow: hidden;
		max-height: 40px;
		max-width: 40px;
		position: relative;
		object-fit: scale-down;
	}
}
.dateName{
	text-align: left;
	padding: 6px;
	margin-left: 20px;
	display: flex;
	flex-direction: column;
	justify-content: space-around;
}

.likes__container{
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	width: 150px;
	margin: 10px 0px;
	.comments, .like, .dislike{
		display: flex;
		flex-direction: row;
		justify-content: space-between;
		background-color: rgb(240, 235, 243);
		transition: background-color 2s ease;
		padding: 2px 7px;
		border-radius: 10px;
	
		align-items: center;
		img{
			margin-right: 5px;
			max-height: 30px;
		}
	}
	.like{
		display: flex;
		flex-direction: row;
		img{
			margin-bottom: 5px;
		}
	   }
	   .dislike{
		flex-direction: row;
		   img{
			   margin-top: 5px;
		   }
	   }
	   .like.active, .dislike.active{
		background-color: rgb(230, 189, 254);
		animation: liked 1.5s linear;
		img{	
		}
	}
	@keyframes liked {
		0%{	
			transform: scale(1);
		}
		70%{
			transform: scale(1.3);
		}
		100%{
			transform: scale(1);
		}
	}
}


@media screen and (max-width:767px) {
	.content{

		padding: 25px;
		background-color: white;
		border-radius: 16px;
		max-width: 100%;
		max-height: 100%;
		img{
			max-width: 100%;
			max-height: 100%;
		}
	}
}
