.onlinePeople{
	position: fixed;
	z-index: 20;
	top: 250px;
	right:150px;
	max-height: 200px;
	box-shadow: 0px 0px 12px 10px rgba(34, 60, 80, 0.2);
	align-items: center;
	width: 50px;
	background-color: rgb(255, 255, 255);
	border-radius: 10px;
	padding: 5px;
	display: flex;
	flex-direction: column;
	strong{
		cursor: grab;
	}
}
.image{
	
	max-width: 40px;
	max-height:40px;
	border-radius: 50%;
	box-shadow: 0px 0px 3px 2px rgba(34, 60, 80, 0.2);

}
.image_block{
	position: relative;
}
.indicator{
	position: absolute;
	width: 12px;
	height: 12px;
	top: 0px;
	right: 0px;
	border-radius: 50%;
	background: linear-gradient(90deg, rgba(200,252,171,1) 0%, rgba(109,218,49,1) 34%, rgba(50,136,2,1) 100%);
	box-shadow: 0px 0px 4px 2px rgb(224, 255, 157);
}
.list{
	display: flex;
	align-items: center;
	flex-direction: column;
	padding: 5px;
	height: 120px;
	overflow: scroll;
	&>img{
		margin-bottom: 5px;
	}
	&::-webkit-scrollbar{

		width: 2px;
		height: 0px;
	}
	&::-webkit-scrollbar-track {
		// box-shadow: 5px 5px 5px -5px rgba(34, 60, 80, 0.2) inset;
		background-color: #f9f9fd;
		border-radius: 10px;
	  }
	  
	&::-webkit-scrollbar-thumb {
		border-radius: 10px;
		background: grey;
	}
}