$LightGray: hsl(274, 100%, 88%);
$LightGrayishBlue:  	rgb(146, 90, 250);
// hsl(238, 48%, 88%)
.navbar{
	z-index: 30;
	position: absolute;
	top: 0;
	// transform: translateZ(0);
	// -webkit-transform: translateZ(0);
	border-radius: 15px;
	position: fixed;
	box-sizing: border-box;
	width: 100vw;
	display: flex;
	// padding: 0px 25vw;
	justify-content: space-between;
	align-items: center;
	background-color: rgba($LightGrayishBlue, 1);
	box-shadow:  0px 0px 5px 10px rgba($LightGray, 0.3) inset;
	margin: 0 auto;
	padding: 0 15vw;
	height: 60px;
 	// padding: 0;
}
.links{
	
	// position: absolute;
	position: fixed;
	z-index: 31;
	top: 10px;
	width: 45%;
	right: 15vw;
	display: flex;
	justify-content: space-between;
	align-items: center;

}
.menuBurger{
	display: none;
}
.auth{
	width: 25%;
}
.image{
	max-height: 80%;
}
.btn{
	background-color: rgba($color: #000000, $alpha: 0);

}
@media (max-width:600px) {
	.navbar{
		padding: 0px 50px;
		z-index: 30;
	}
	.image{
		// j;
	}
	.links{
		// display: none;
		z-index: 1;
		
		top: 0;
		flex-direction: column;
		width: 30vw;
		height: 100vh;
		right: 0;
		// margin-top: 60px;
		padding: 70px 0px 50vh 0px;
		background-color: $LightGrayishBlue;
	}
	.menuBurger{
		display: block;
	}
}