.today{
	width:100% ; 
	height:100%;
	padding:20px; 
	text-align: left;
	display: flex;
	flex-direction: column;
}
.mainInfo{
	flex: 0 1 10%;
	display:flex;
	justify-content: space-between;
	font-size: 26px;
	margin-bottom: 10px;
	
}
.datetime{
	grid-area: datetime;
	
}
.temperature{
	grid-area: temperature;
	display: flex;
	justify-content: center;
	align-content: center;
}
.temperature img{
	width: 50px;
	height: 50px;
}
.temperature div{
	font-size: 30px;
	align-self: center;
}
.place{

}
.feels{
	flex: 0 1 15%;
}
.conditions{
	display: grid;
	grid-template-columns: 1fr 1fr;
	column-gap: 10px;
	row-gap: 10px;
	flex: 0 0 40%;
}
